import React from "react";
// import Unity from "react-unity-webgl"
import { Unity, useUnityContext } from "react-unity-webgl";

function App() {
  const { unityProvider } = new useUnityContext({
    dataUrl: "https://aleccross.github.io/runner-game-unity3d-webgl-reactjs/build/ReactBuild.data",
    frameworkUrl: "https://aleccross.github.io/runner-game-unity3d-webgl-reactjs/build/ReactBuild.framework.js",
    loaderUrl: "https://aleccross.github.io/runner-game-unity3d-webgl-reactjs/build/ReactBuild.loader",
    codeUrl: "https://aleccross.github.io/runner-game-unity3d-webgl-reactjs/build/ReactBuild.wasm",
    webglContextAttributes: {
      preserveDrawingBuffer: true,
    },
  })
  return <div style={{ textAlign: "center" }}>
    <h1>ReactBuild-ReactJS</h1>
    <Unity unityProvider={unityProvider} style={{ width: 800, height: 600, border: "solid black 1px" }} />
  </div>
}

export default App
